.promo {
  position: relative;

  margin-bottom: 27px;

  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% -18px;
  background-color: $promo-background;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 575px auto;
  }
}

.promo__wrapper {
  display: flex;
  flex-direction: column;
}

.promo__title {
  align-self: center;
  width: 244px;
  margin: 0;
  margin-top: 71px;
  margin-bottom: 122px;

  font-size: 27px;
  line-height: 30px;
  font-weight: 700;
  color: $primary-text-light;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .promo {
    margin-top: -43px;
    margin-bottom: 35px;

    background-image: url("../img/hero-tablet@1x.jpg");
    background-position-y: 0;
    box-shadow: inset 0 1px 0 0 $border-card;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
      background-size: 1150px auto;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;

      display: block;
      width: 100%;
      height: 24px;

      background-image: url("../img/bg-zigzag-fill.svg");
      background-repeat: repeat-x;
    }
  }

  .promo__title {
    width: 500px;
    margin-top: 155px;
    margin-bottom: 373px;

    font-size: 43px;
    line-height: 50px;
  }
}

@media (min-width: $desktop-width) {
  .promo {
    margin-bottom: 114px;
    background-image: url("../img/hero-desktop@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
    }
  }

  .promo__title {
    width: 620px;
    margin-top: 154px;
    margin-bottom: 359px;

    font-size: 53px;
    line-height: 57px;
  }
}
